<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="querInfo.query" clearable @clear="getGoriesList">
            <el-button slot="append" icon="el-icon-search"  @click="getGoriesList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table stripe :data="goriseList" border style="width: 100%">
        <el-table-column type="index" label="#" ></el-table-column>
        <el-table-column prop="order_number" label="" width="400"></el-table-column>
        <el-table-column prop="order_price" label="()" ></el-table-column>
        <el-table-column prop="pay_status" label="" >
          <template slot-scope="scope">
            <el-tag v-if=" scope.row.pay_status === '1'" type="success"></el-tag>
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" ></el-table-column>
        <el-table-column prop="create_time" label="" width="170">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column prop="goods_number" label="" width="150">
          <el-tooltip class="item" effect="dark" content="" placement="top">
            <el-button type="primary" icon="el-icon-edit" @click="orderDialogVisible = true" ></el-button>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="" placement="top">
            <el-button type="success" icon="el-icon-truck" @click="showDialogVisibleOk"></el-button>
          </el-tooltip>
        </el-table-column>
      </el-table>
      <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="querInfo.pagenum"
            :page-sizes="[7, 15, 30, 100]"
            :page-size="querInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="orderDialogVisible"
      width="65%"
      :before-close="null">
     <el-form :model="orderRuleForm" :rules="orderRules" ref="rorderRuleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="/" prop="address1">
             <el-cascader :options="cityData" v-model="orderRuleForm.address1" style=width:100%>
             </el-cascader>
         </el-form-item>
       <el-form-item label="" prop="address2">
         <el-input v-model="orderRuleForm.address2"></el-input>
       </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="orderDialogVisible = false"> </el-button>
        <el-button type="primary" @click="rorderRuleFormOk"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="DialogVisible"
      width="55%">
      <div class="block">
       <div class="radio">
          
          <el-radio-group v-model="reverse">
            <el-radio :label="true"></el-radio>
            <el-radio :label="false"></el-radio>
          </el-radio-group>
       </div>
       <el-timeline :reverse="reverse">
          <el-timeline-item
            v-for="(activity, index) in activitieslist"
            :key="index"
            :timestamp="activity.context">
            {{activity.time}}
          </el-timeline-item>
        </el-timeline>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="DialogVisible = false"> </el-button>
        <el-button type="primary" @click="DialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata.js'
export default {
  data () {
    return {
      // 
      querInfo: {
        query: '',
        pagenum: 1,
        pagesize: 7
      },
      // 
      reverse: true,
      // 
      activitieslist: [],
      // 
      orderRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityData,
      // 
      orderRuleForm: {
        address1: [],
        address2: ''
      },
      orderDialogVisible: false,
      // 
      DialogVisible: false,
      goriseList: [],
      total: 0
    }
  },
  created () {
    this.getGoriesList()
  },
  methods: {
    async getGoriesList () {
      const { data: res } = await this.$http.get('orders', { params: this.querInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.goriseList = res.data.goods
      this.total = res.data.total
    },
    // 
    async showDialogVisibleOk () {
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.activitieslist = res.data
      this.DialogVisible = true
    },
    // 
    rorderRuleFormOk () {
      this.$refs.rorderRuleForm.validate((valid) => {
        if (valid) {
          this.$message.success('')
          this.orderDialogVisible = false
        } else {
          return false
        }
      })
    },
    handleSizeChange (newSize) {
      this.querInfo.pagesize = newSize
      this.getGoriesList()
    },
    handleCurrentChange (newNum) {
      this.querInfo.pagenum = newNum
      this.getGoriesList()
    }
  }
}
</script>

<style>
  .radio{
    padding-bottom: 20px;
  }
</style>
